/* client/src/components/HistoryModal.css */

.history-modal-overlay {
  position: fixed;
  inset: 0;
  background-color: rgba(0, 0, 0, 0.75);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: var(--z-index-modal);
  backdrop-filter: blur(4px);
  padding: var(--spacing-5); /* Use theme variable */
  box-sizing: border-box;
}

.history-modal-content {
  background-color: var(--dark-bg-header); /* Use theme variable */
  color: var(--dark-text-primary); /* Use theme variable */
  padding: var(--spacing-5) var(--spacing-6); /* Use theme variables */
  border-radius: var(--borderRadius-lg); /* Use theme variable */
  width: 90%;
  max-width: 1200px;
  height: 85vh;
  max-height: 800px;
  box-shadow: var(--shadow-xl); /* Use theme variable */
  position: relative;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  border: 1px solid var(--dark-border-color); /* Add border from theme */
}

.history-modal-close-btn {
  position: absolute;
  top: var(--spacing-3); /* Use theme variable */
  right: var(--spacing-3); /* Use theme variable */
  background: transparent;
  border: none;
  font-size: var(--typography-fontSize-3xl); /* Use theme variable */
  font-weight: var(--typography-fontWeight-bold); /* Use theme variable */
  color: var(--dark-text-secondary); /* Use theme variable */
  cursor: pointer;
  line-height: 1;
  padding: var(--spacing-1); /* Use theme variable */
  transition: color var(--transitions-fast); /* Use theme variable */
}

.history-modal-close-btn:hover {
  color: var(--dark-text-primary); /* Use theme variable */
}

.history-modal-content h2 {
  margin: 0 0 var(--spacing-4) 0; /* Use theme variable */
  padding-bottom: var(--spacing-3); /* Use theme variable */
  text-align: center;
  font-weight: var(--typography-fontWeight-semibold); /* Use theme variable */
  color: var(--dark-text-primary); /* Use theme variable */
  border-bottom: 1px solid var(--dark-border-color); /* Use theme variable */
  flex-shrink: 0;
}

.history-error,
.history-loading,
.history-empty {
  color: var(--dark-text-secondary); /* Use theme variable */
  padding: var(--spacing-3) var(--spacing-4); /* Use theme variables */
  border-radius: var(--borderRadius-md); /* Use theme variable */
  margin-bottom: var(--spacing-4); /* Use theme variable */
  text-align: center;
  font-size: var(--typography-fontSize-sm); /* Use theme variable */
  font-style: italic;
}

.history-error {
  color: var(--error-500); /* Use theme variable */
  background-color: var(--error-500/10); /* Use direct color/opacity */
  border: 1px solid var(--error-500); /* Use theme variable */
  font-style: normal;
}

.history-layout {
  display: flex;
  flex-grow: 1;
  gap: var(--spacing-5); /* Use theme variable */
  overflow: hidden;
}

.history-session-list {
  width: 320px;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  padding-right: var(--spacing-3); /* Use theme variable */
  border-right: 1px solid var(--dark-border-color); /* Use theme variable */
}

.history-session-list h3 {
  margin: 0 0 var(--spacing-3) 0; /* Use theme variable */
  padding: 0 var(--spacing-1); /* Use theme variable */
  font-size: var(--typography-fontSize-base); /* Use theme variable */
  font-weight: var(--typography-fontWeight-semibold); /* Use theme variable */
  color: var(--dark-text-secondary); /* Use theme variable */
  flex-shrink: 0;
}

.history-session-list ul {
  list-style: none;
  padding: 0;
  margin: 0;
  flex-grow: 1;
  overflow-y: auto;
}

.history-session-list li {
  padding: var(--spacing-2) var(--spacing-3); /* Use theme variables */
  margin-bottom: var(--spacing-2); /* Use theme variable */
  border: 1px solid transparent;
  border-radius: var(--borderRadius-sm); /* Use theme variable */
  cursor: pointer;
  transition: background-color var(--transitions-fast), border-color var(--transitions-fast); /* Use theme variables */
  background-color: var(--dark-bg-input); /* Use theme variable */
}

.history-session-list li:hover {
  background-color: var(--neutral-800); /* Use theme variable */
  border-color: var(--neutral-700); /* Use theme variable */
}

.history-session-list li.active {
  background-color: var(--primary-500); /* Use theme variable */
  border-color: var(--primary-500); /* Use theme variable */
  color: var(--neutral-50); /* Use theme variable */
}

.history-session-list li.active .session-date {
  color: rgba(255, 255, 255, 0.85); /* Keep specific opacity for contrast */
}

.session-preview {
  font-size: var(--typography-fontSize-sm); /* Use theme variable */
  font-weight: var(--typography-fontWeight-medium); /* Use theme variable */
  margin-bottom: var(--spacing-1); /* Use theme variable */
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.session-date {
  font-size: var(--typography-fontSize-xs); /* Use theme variable */
  color: var(--dark-text-secondary); /* Use theme variable */
  margin-top: var(--spacing-1); /* Use theme variable */
}

.history-session-details {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  background-color: var(--dark-bg-main); /* Use theme variable */
  border-radius: var(--borderRadius-md); /* Use theme variable */
  border: 1px solid var(--dark-border-color); /* Add border from theme */
}

.history-session-details h3 {
  margin: 0;
  padding: var(--spacing-3) var(--spacing-5); /* Use theme variables */
  font-size: var(--typography-fontSize-base); /* Use theme variable */
  font-weight: var(--typography-fontWeight-semibold); /* Use theme variable */
  color: var(--dark-text-secondary); /* Use theme variable */
  border-bottom: 1px solid var(--dark-border-color); /* Use theme variable */
  background-color: var(--dark-bg-header); /* Use theme variable */
  border-top-left-radius: var(--borderRadius-md); /* Use theme variable */
  border-top-right-radius: var(--borderRadius-md); /* Use theme variable */
  flex-shrink: 0;
}

.history-session-details > .history-empty,
.history-session-details > .history-loading {
  padding: var(--spacing-8); /* Use theme variable */
  text-align: center;
  flex-grow: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

.history-messages-area {
  flex-grow: 1;
  overflow-y: auto;
  padding: var(--spacing-5); /* Use theme variable */
  display: flex;
  flex-direction: column;
  gap: var(--spacing-4); /* Use theme variable */
}

/* Scrollbar styles handled by global.css */

.history-message {
  display: flex;
  max-width: 85%;
  position: relative;
  word-wrap: break-word;
  flex-direction: column;
}

.history-message.user {
  align-self: flex-end;
  align-items: flex-end;
}

.history-message.model {
  align-self: flex-start;
  align-items: flex-start;
}

.history-message-content {
  padding: var(--spacing-3) var(--spacing-4); /* Use theme variables */
  border-radius: var(--borderRadius-lg); /* Use theme variable */
  font-size: var(--typography-fontSize-sm); /* Use theme variable */
  line-height: var(--typography-lineHeight-normal); /* Use theme variable */
  box-shadow: var(--shadow-sm); /* Use theme variable */
  text-align: left;
}

.history-message.user .history-message-content {
  background-color: var(--primary-600); /* Use theme variable */
  color: var(--neutral-50); /* Use theme variable */
  border-bottom-right-radius: var(--borderRadius-sm); /* Use theme variable */
}

.history-message.model .history-message-content {
  background-color: var(--dark-bg-secondary); /* Use theme variable */
  color: var(--dark-text-primary); /* Use theme variable */
  border: 1px solid var(--dark-border-color); /* Use theme variable */
  border-bottom-left-radius: var(--borderRadius-sm); /* Use theme variable */
}

/* Markdown styles within messages (partially handled by global.css, but some specifics here) */
/* p, strong, em, ul, ol, li handled by global.css */

.history-message-content pre {
  background-color: var(--dark-bg-main); /* Use theme variable */
  border: 1px solid var(--dark-border-color); /* Use theme variable */
  border-radius: var(--borderRadius-md); /* Use theme variable */
  padding: var(--spacing-3); /* Use theme variable */
  overflow-x: auto;
  font-family: var(--typography-fontFamily-mono); /* Use theme variable */
  font-size: var(--typography-fontSize-xs); /* Use theme variable */
  margin: var(--spacing-3) 0; /* Use theme variable */
  white-space: pre-wrap; /* Use pre-wrap for better wrapping */
  color: var(--dark-text-primary); /* Use theme variable */
}

.history-message-content code {
  font-family: var(--typography-fontFamily-mono); /* Use theme variable */
  font-size: var(--typography-fontSize-xs); /* Use theme variable */
  background-color: var(--dark-bg-input); /* Use theme variable */
  padding: var(--spacing-1) var(--spacing-2); /* Use theme variables */
  border-radius: var(--borderRadius-sm); /* Use theme variable */
  border: 1px solid var(--dark-border-color); /* Use theme variable */
  color: var(--dark-text-secondary); /* Use theme variable */
}

.history-message-content pre code {
  background-color: transparent;
  padding: 0;
  border: none;
  font-size: inherit;
  color: inherit;
}

.history-message-timestamp {
  font-size: var(--typography-fontSize-xs); /* Use theme variable */
  color: var(--dark-text-tertiary); /* Use theme variable */
  margin-top: var(--spacing-1); /* Use theme variable */
  padding: 0 var(--spacing-1); /* Use theme variable */
}

.history-message-error {
  color: var(--error-500); /* Use theme variable */
  font-style: italic;
  padding: var(--spacing-1) 0; /* Use theme variable */
  text-align: center;
  font-size: var(--typography-fontSize-xs); /* Use theme variable */
}

/* Mobile Responsiveness */
@media (max-width: var(--breakpoint-md)) {
  .history-modal-content {
    padding: var(--spacing-4); /* Adjust padding */
    height: 90vh; /* Increase height slightly */
    max-height: none; /* Remove max height limit */
  }

  .history-layout {
    flex-direction: column; /* Stack sections */
    gap: var(--spacing-4); /* Adjust gap */
  }

  .history-session-list {
    width: 100%; /* Full width when stacked */
    border-right: none; /* Remove right border */
    padding-right: 0; /* Remove padding */
    border-bottom: 1px solid var(--dark-border-color); /* Add bottom border */
    flex-shrink: 1; /* Allow shrinking */
  }

  .history-session-list ul {
      max-height: 150px; /* Limit session list height */
      overflow-y: auto; /* Ensure scrolling */
  }

  .history-session-details {
    flex-grow: 1; /* Allow details to take available space */
    border: none; /* Remove main border */
    border-radius: 0; /* Remove border radius */
  }

   .history-session-details h3 {
       border-top-left-radius: 0;
       border-top-right-radius: 0;
   }

  .history-messages-area {
    padding: var(--spacing-4); /* Adjust padding */
    gap: var(--spacing-3); /* Adjust gap */
  }

  .history-message {
    max-width: 100%; /* Allow messages to take more width */
  }

  .history-modal-close-btn {
      font-size: var(--typography-fontSize-2xl); /* Adjust size */
  }
}

@media (max-width: var(--breakpoint-sm)) {
    .history-modal-content {
        padding: var(--spacing-3); /* Further adjust padding */
    }

    .history-messages-area {
        padding: var(--spacing-3); /* Further adjust padding */
        gap: var(--spacing-2); /* Further adjust gap */
    }

    .history-message-content {
        padding: var(--spacing-2) var(--spacing-3); /* Adjust message padding */
        font-size: var(--typography-fontSize-xs); /* Adjust message font size */
    }

    .history-session-list h3,
    .history-session-details h3 {
        font-size: var(--typography-fontSize-sm); /* Adjust heading font size */
    }

     .session-preview {
        font-size: var(--typography-fontSize-xs); /* Adjust preview font size */
    }

    .session-date {
        font-size: var(--typography-fontSize-xs); /* Adjust date font size */
    }

    .history-message-content pre,
    .history-message-content code {
         font-size: var(--typography-fontSize-xs); /* Adjust code font size */
    }
} 